<template>
<div class="w-full">
    <!-- Content -->
    <section class="w-full neu-in rounded-xl">
          <div class="neu-out rounded-xl p-5 py-8">

              <div class="flex flex-wrap">
                <div class="w-full lg:w-1/2 px-5 flex flex-wrap">
                 <label class="text-left  pl-2 w-full my-3" for="nama">Logo</label>

                     <croppa class="mx-auto neu-in rounded-full"
                          v-model="logo"
                          :width="150"
                          prevent-white-space
                          :height="150"
                          @init="onInit"
                          ></croppa>
         

                  <label class="text-left  pl-2 w-full my-3" for="nama">Name</label>
                      <input class="w-full
                        
                      neu-in bg-theme_primary_light
                      py-2 px-8 
                      rounded-full mb-3
                      " 
                      type="text"
                      v-model="d.name"
                      placeholder="Name">
                 
                 
                </div>
                <div class="w-full lg:w-1/2 px-5 flex flex-wrap">

                 <label class="text-left  pl-2 w-full my-3" for="no_hp">Phone Number / WA</label>
                  <input class="w-full
                            
                          neu-in bg-theme_primary_light
                          py-2 px-8 
                          rounded-full mb-3
                          " 
                          type="text"
                          v-model="d.phone"
                          placeholder="No Hp">

                 <label class="text-left  pl-2 w-full my-3" for="email">Email</label>
                     <input class="w-full
                            
                          neu-in bg-theme_primary_light
                          py-2 px-8 
                          rounded-full mb-3
                          " 
                          type="text"
                          v-model="d.email"
                          placeholder="Email">
                    <label class="text-left  pl-2 w-full my-3"  for="email">Descriptions</label>
                      <textarea class="w-full
                            
                          neu-in bg-theme_primary_light
                          py-5 px-8 
                          rounded-xl mb-3

                          h-32
                          " 
                          v-model="d.desc"
                          placeholder="Descriptions">
                      </textarea>

                      <button 
                      @click="form = true"
                      class="w-full  py-2 px-8 bg-primary_dark hover:opacity-75 rounded-full mt-5">
                          <i class="fa fa-plus"></i>
                          Register
                      </button>

                </div>
                 
                 
              </div>

        </div>
    </section>
</div>
        
</template>

<script>
import Vue from 'vue'
import Croppa from 'vue-croppa'
import 'vue-croppa/dist/vue-croppa.css'


 Vue.use(Croppa) 

export default {
  data(){
    return {
       logo: {},
       d:{
         logo: '',
         name: '',
         phone: '',
         email: '',
         desc: ''
         
       }
    }
  },
  methods:{
     onInit() {
          this.logo.addClipPlugin(function (ctx, x, y, w, h) {
              ctx.beginPath()
              ctx.arc(x + w / 2, y + h / 2, w / 2, 0, 2 * Math.PI, true)
              ctx.closePath()
          })
      }
  }
}

</script>